
.rightbar{
    flex:3.5;
}

.rightbarWrapper{
    padding: 20px 20px 0 0;
}

.birthdayContainer{
    display:flex;
    align-items: center;
    margin-bottom: 50px;
    margin-left: 30px;
}

.birthdayImg{
    width: 40px;
    height: 40px;
    margin-right: 10px;
}

.birthdayText{
    font-weight:300;
    font-size:15px;
}

.rightbarTitle{
    margin-bottom: 20px;
    margin-left: 30px;
}
.rightbarFriendList{
    padding:0;
    margin:0;
    list-style:none;
}

.rightbarTitle{
    font-size:18px;
    font-weight: 500;
    margin-bottom: 10px;
}

.rightbarInfo{
    margin-bottom:30px;

}

.rightbarInfoItem{
    margin-bottom:10px;
}

.rightbarInfoKey{
    font-weight: 500;
    margin-right: 15px;
     color:#555;
}
.rightbarInfoValue{
    font-weight: 200;
}

.rightbarFollowings{
    display:flex;
    flex-wrap:wrap;
    justify-content: space-between;
}

.rightbarFollowing{
    display:flex;
    flex-direction: column;
    margin-bottom:20px;
    cursor:pointer;
}

.rightbarFollowingImg{
    width: 100px;
    height: 100px;
    object-fit: cover;
    border-radius:5px;

}

.rightbarFollowButton{
    margin-top: 30px;
    margin-bottom: 10px;
    border: none;
    background-color: rgb(253, 213, 220);
    color: white;
    border-radius:5px;
    padding: 5px 10px;
    display: flex;
    align-items: center;
    font-size:16px; 
    font-weight: 500;
    cursor: pointer;
}