.community{
    display:flex;
}

.communityRight{
    flex:9;
    background-color: orange;
}
.communityCover{
    height: 320px;
    position: relative;
    background-color: aqua;
}
.communityCoverImg{
    width: 100%;
    height: 250px;
    object-fit:cover;
}
.communityRightCenter{
    flex:5.5;
    background-color: aqua;
}
