    flex:3.5;
} */
.communityRightBottom{
    display:flex;
    margin-top:70px;
}
.communityTitle{
    display: flex;
    background-color: red;
    height: 100px;
    align-items: center;
    justify-content: center;
    margin-bottom: 40px;
    color: aliceblue;
    font-size:24px;
}