.messenger{
    height: calc(100vh - 70px);
    display: flex;
}

.chatMenu{
    flex:3.5;
    background-color: lightyellow;
}
.chatBox{
    flex:5.5;
    background-color: lightblue;
}

.chatBoxWrapper{
    display: flex;
    flex-direction: column;
    position:relative;
}

.chatBoxTop{
    height: 100%;
    overflow-y: scroll;
    padding-right: 10px;
}

.chatBoxBottom{
    margin-top: 5px;
    display: flex;
    align-items: center;
    justify-content:space-between;
}

.chatMessageInput{
    width: 80%;
    height: 90px;
    padding: 10px;
}
.chatSubmitButton{
    width: 70px;
    height: 40px;
    border:none;
    border-radius: 5px;
    cursor: pointer;
    background-color: lightseagreen;
    color:white;
}
.chatOnline{
    flex:3;
    background-color: lightcoral;
}
.chatMenuWrapper,.chatBoxWrapper,.chatOnlineWrapper{
    padding:10px;
    height: 100%;
}

.noConversationText{
    position: absolute;
    top: 10%;
    font-size:50px;
    color: gray;
    text-align: center;
    cursor: default;
}
.chatMenuInput{
    width: 90%;
    padding: 10px 0;
    border: none;
    border-bottom: 1px solid grey;
}
