
.communityRightbar{
    flex:3.5;
    background-color: blueviolet;
}

.communityRightbarWrapper{
    padding: 20px 20px 0 0;
}
.communityRightbarMembers{
    display:flex;
    flex-wrap:wrap;
    justify-content: space-between;
}

.communityRightbarMember{
    display:flex;
    flex-direction: column;
    margin-bottom:20px;
    cursor:pointer;
}
.communityRightbarMembersTitle{
    display: flex;
    align-items:  center;
    justify-content: center;
    font-size: 28px;
}