.topbarContainer{
    height:50px;
    width:100%;
    background-color:pink;
    display: flex;
    align-items:center;
    position:sticky;
    top:0;
    z-index: 999 ;
}

.topbarLeft{
    flex:3;
}
.logo{
    font-size:24px;
    margin-left:20px;
    font-weight:bold;
    color:white;
    cursor:pointer;

}

.searchbar{
    width: 100%;
    height: 30px;
    border-radius:30px;
    display:flex; 
    align-items: center;
}
.topbarCenter{
    flex:5;
}

.searchIcon{
    font-size:20px !important;
    margin-left: 10px;
    
}
.searchInput{
    border:none;
    width:70%;
    border-radius:30px;

}
.searchInput:focus{
    outline:none;
}
.topbarRight{
    flex:4;
    display: flex;
    align-items:center;
    justify-content: space-around;
    /* space-between */
    
}

.topbarLink{
    margin-right :10px;
    font-size:14px;
    cursor:pointer;
    
    
}
.topbarIcons{

    display: flex;
}
.topbarIconItem{
    margin-right:15px;
    cursor:pointer;
    position:relative;
    /* becuase the badge will be absolute, we can position items according to the badge. */
}
.topbarIconBadge{

    width: 15px;
    height: 15px;
    background-color: red;
    color: white;
    border-radius: 50%;
    position:absolute;
    top: -5px;
    right: -5px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size:12px;
}

.topbarImg{

    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    /* what happens without object-fit , => image is squisuhed, not good*/
    cursor: pointer;    
}